import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class LoginService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    login(userName, password,AdminUserType) {
        let body = JSON.stringify({ UserName: userName, Password: password,UserType:AdminUserType });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/login", body, options)
            .map(res => res.json());
    }
    ChemistloginService(Chemistusername,Chemistpassword,ChemistUserType)
    {
        let body = JSON.stringify({ UserName: Chemistusername, Password: Chemistpassword,UserType:ChemistUserType });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/login", body, options)
            .map(res => res.json());

    }
}


