import { Component, ViewEncapsulation } from '@angular/core';
import { LoginService } from './../services/LoginService';
import { Router, ActivatedRoute, Params } from '@angular/router';
@Component({
  selector: 'login',
  styles: [ require('./login.style.scss') ],
  template: require('./login.template.html'),
  providers: [LoginService],
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'login-page app'
  }
})
export class Login {
  public isAdmin:boolean;
  public isChemist:boolean;
  
  public username:String;
  public Chemistusername:any;
  public Chemistpassword:any;
  public password:String;
  public router:Router;
  public AdminUserType:any;
  public ChemistUserType:any;


  constructor(public _loginService: LoginService, router: Router, ) {
    this.router = router;
    
    
  }
  callLoginApi(){
      if(this.username==undefined && this.password==undefined)
      {
alert("please fill empty fields");

      }
      else{
       this.AdminUserType=1;
      
        if (this.username != undefined && this.password != "" && this.password.trim().length != 0) {
          this._loginService.login(this.username, this.password,this.AdminUserType).subscribe(
            response => {
              console.log(response);
          
              localStorage.setItem("UserType",response.data.UserType);
             
              console.log(localStorage.getItem("UserType"));
              jQuery("#snackbar").html(response.message);
              this.myFunction();

            this.router.navigate(["/app/dashboard"]);
           
           
            });
        }
        else
        {

          jQuery("#snackbar").html("Invalid Credenntials");
          this.myFunction();
        }

      }
  }
  callChemistLoginApi()
  {
    if(this.Chemistusername==undefined && this.Chemistpassword==undefined)
    {
alert("please fill empty fields");

    }
    else{
      this.ChemistUserType=2;
     
      if (this.Chemistusername != undefined && this.Chemistpassword != "" && this.Chemistpassword.trim().length != 0) {
        this._loginService.ChemistloginService(this.Chemistusername, this.Chemistpassword,this.ChemistUserType).subscribe(
          response => {
            console.log(response);
        
      
            localStorage.setItem("UserType",response.data.UserType);
            localStorage.setItem("ChemistId",response.data.ChemistId);
            console.log(localStorage.getItem("UserType"));
            jQuery("#snackbar").html(response.message);
            this.myFunction();
          this.router.navigate(["/app/dashboard"]);
        
          });
      }
      else
      {

        jQuery("#snackbar").html("Invalid Credenntials");
        this.myFunction();
      }

    }

  }

myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
}



}



